"use client"

import { useEffect, useState, Suspense } from "react"
import { useParams, useRouter } from "next/navigation"
import dynamic from "next/dynamic"
import { Button } from "@/components/ui/button"
import CommentsSection from "@/components/comments-section"
import ReactionsBar from "@/components/reactions-bar"
import ReviewsSection from "@/components/reviews-section"
// Import icons for visual flair (assuming lucide-react or similar)
import { ShoppingCart, BookOpen, Layers, DollarSign } from "lucide-react"

// Dynamically import PdfViewer with ssr: false
const PdfViewer = dynamic(() => import("@/components/pdf-viewer"), {
  ssr: false,
  loading: () => (
    <div className="text-center py-12">
      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
      <p className="text-lg text-muted-foreground">Loading E-Book Preview...</p>
    </div>
  ),
})

interface Book {
  _id: string
  title: string
  description: string
  price: number
  type: string
  cover: string
  isbn: string
  filePath?: string
  stock?: number
}

// **Aesthetic Utility Class (Conceptual):** // Assuming a custom class or Tailwind utility for a subtle fade-up/slide-in animation on mount.
// We'll use a placeholder class like "animate-fade-in-up"

export default function BookDetailPage() {
  const params = useParams()
  const router = useRouter()
  const id = params.id as string
  const [book, setBook] = useState<Book | null>(null)
  const [loading, setLoading] = useState(true)

  // --- Fetch Logic (Unchanged for Functionality) ---
  useEffect(() => {
    async function fetchBook() {
      try {
        const res = await fetch(`/api/books/${id}`)
        if (res.ok) {
          const data = await res.json()
          setBook(data)
        }
      } catch (error) {
        console.error("Failed to fetch book:", error)
      } finally {
        setLoading(false)
      }
    }
    fetchBook()
  }, [id])

  // --- Add to Cart Logic (Unchanged for Functionality) ---
  const handleAddToCart = async () => {
    if (!book) return
    try {
      const res = await fetch("/api/cart", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          bookId: id,
          quantity: 1,
          price: book.price,
        }),
      })
      if (res.ok) {
        // Use a more aesthetic toast/notification library instead of alert()
        console.log("Added to cart!") // Placeholder for a beautiful notification
        router.push("/cart")
      } else {
        alert("Please login to add items to cart")
      }
    } catch (error) {
      console.error("Failed to add to cart:", error)
    }
  }

  // --- Loading States ---
  if (loading) return (
    <div className="flex items-center justify-center min-h-screen bg-background">
      <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
      <p className="ml-4 text-xl text-primary">Fetching Book Details...</p>
    </div>
  )
  if (!book) return <div className="py-24 text-center text-2xl font-semibold text-destructive">Book not found üòî</div>

  // --- Rendered Component (Aesthetic Changes) ---
  return (
    // Use a softer background color and padding
    <main className="min-h-screen bg-gray-50 dark:bg-gray-900 py-16 px-4 sm:px-6 lg:px-8"> 
      <div className="max-w-5xl mx-auto">
        
        {/* Book Header - Added shadow and rounded corners */}
        <div className="grid md:grid-cols-2 gap-10 p-6 bg-white dark:bg-card shadow-xl rounded-xl mb-12 animate-fade-in-up"> 
          
          {/* Cover Area */}
          <div className="aspect-[3/4] bg-muted rounded-lg overflow-hidden shadow-lg transform hover:scale-[1.01] transition-transform duration-300">
            {book.cover ? (
              <img 
                src={book.cover || "/placeholder.svg"} 
                alt={book.title} 
                className="w-full h-full object-cover" 
              />
            ) : (
              // Enhanced "No Cover" Placeholder
              <div className="w-full h-full flex flex-col items-center justify-center bg-gradient-to-br from-primary/10 to-background/50 p-6">
                <BookOpen className="w-12 h-12 text-primary/70 mb-2" />
                <span className="text-lg font-medium text-muted-foreground">Cover Not Available</span>
              </div>
            )}
          </div>

          {/* Details & Actions */}
          <div className="flex flex-col justify-between pt-4 pb-2">
            <div>
              {/* Enhanced Title Typography */}
              <h1 className="text-5xl font-extrabold text-foreground mb-4 leading-tight">{book.title}</h1>
              <p className="text-xl text-gray-500 dark:text-gray-400 mb-8 font-light italic">{book.description.substring(0, 150)}...</p>

              {/* Metadata Badges/Tags */}
              <div className="space-y-3 mb-8">
                <div className="flex items-center text-sm text-muted-foreground">
                  <Layers className="w-4 h-4 mr-2 text-primary/70" />
                  <span>ISBN: <span className="font-medium text-foreground">{book.isbn}</span></span>
                </div>
                {book.stock !== undefined && (
                  <div className="flex items-center text-sm text-muted-foreground">
                    <Layers className="w-4 h-4 mr-2 text-primary/70" />
                    <span>Stock: <span className={`font-medium ${book.stock > 0 ? "text-green-600 dark:text-green-400" : "text-red-600 dark:text-red-400"}`}>{book.stock} {book.stock > 0 ? "In Stock" : "Out of Stock"}</span></span>
                  </div>
                )}
                <div className="flex items-center text-sm text-muted-foreground">
                  <BookOpen className="w-4 h-4 mr-2 text-primary/70" />
                  <span>Type: <span className="font-medium text-foreground capitalize">{book.type}</span></span>
                </div>
              </div>
            </div>

            <div className="space-y-4 pt-4">
              {/* Highlighted Price */}
              <div className="flex items-center text-4xl font-extrabold text-primary">
                <DollarSign className="w-6 h-6 mr-1" />
                {book.price.toFixed(2)}
              </div>
              <div className="flex gap-4">
                {/* Primary Button with Icon */}
                <Button 
                  onClick={handleAddToCart} 
                  className="flex-1 text-lg py-7 shadow-lg hover:shadow-xl transition-all duration-300 group"
                  disabled={book.stock !== undefined && book.stock <= 0}
                >
                  <ShoppingCart className="w-5 h-5 mr-2 group-hover:animate-bounce" /> 
                  {book.stock !== undefined && book.stock <= 0 ? "Out of Stock" : "Add to Cart"}
                </Button>
              </div>
            </div>
          </div>
        </div>

        {/* Content Area - Added shadow and rounded corners */}
        <div className="bg-white dark:bg-card p-8 rounded-xl shadow-lg border border-border mb-12 animate-fade-in-up delay-100">
          <h2 className="text-3xl font-bold text-foreground mb-4 border-b pb-2 border-primary/20">Full Description</h2>
          <p className="text-lg text-gray-700 dark:text-gray-300 leading-relaxed indent-6">{book.description}</p>
        </div>

        {/* Embedded PDF Viewer */}
        {book.type !== "physical" && book.filePath && (
          <div className="mb-12 bg-white dark:bg-card p-6 rounded-xl shadow-lg border border-border animate-fade-in-up delay-200">
            <h2 className="text-3xl font-bold text-foreground mb-4 border-b pb-2 border-primary/20 flex items-center">
              <BookOpen className="w-6 h-6 mr-2 text-primary" /> Read E-book Preview
            </h2>
            {/* The dynamically imported component with its built-in loading state */}
            <Suspense fallback={<div className="text-center py-8">Loading PDF viewer...</div>}>
              <PdfViewer fileUrl={book.filePath} />
            </Suspense>
          </div>
        )}

        {/* Interactions and Reviews - Added separation and grouping */}
        <div className="space-y-12">
            <div className="p-6 bg-white dark:bg-card rounded-xl shadow-lg border border-border animate-fade-in-up delay-300">
                <h2 className="text-3xl font-bold text-foreground mb-4 border-b pb-2 border-primary/20">Reactions</h2>
                <ReactionsBar contentId={id} contentType="book" />
            </div>

            <div className="p-6 bg-white dark:bg-card rounded-xl shadow-lg border border-border animate-fade-in-up delay-400">
                <h2 className="text-3xl font-bold text-foreground mb-4 border-b pb-2 border-primary/20">Customer Reviews</h2>
                <ReviewsSection contentId={id} contentType="book" />
            </div>

            <div className="p-6 bg-white dark:bg-card rounded-xl shadow-lg border border-border animate-fade-in-up delay-500">
                <h2 className="text-3xl font-bold text-foreground mb-4 border-b pb-2 border-primary/20">Comments</h2>
                <CommentsSection contentId={id} contentType="book" />
            </div>
        </div>

      </div>
    </main>
  )
}